<template>
  <div class="container">
    <h3 class="py-1">
      <span class="badge badge-info">
        {{ getName($route.params.code) }}
      </span>

      <ul class="nav justify-content-center">
        <li class="nav-item">
          <Button class="btn btn-outline-secondary" @click="today()">
            오늘
          </Button>
        </li>
        <li class="nav-item">
          <Button class="btn btn-outline-secondary" @click="tomorrow()">
            내일
          </Button>
        </li>
      </ul>
    </h3>
    <div class="meal-type">
      <ul class="nav justify-content-center">
        <li class="nav-item">
          <Button class="btn btn-outline-primary" @click="load(1)">
            아침
          </Button>
        </li>
        <li class="nav-item">
          <Button class="btn btn-outline-primary" @click="load(2)">
            점심
          </Button>
        </li>
        <li class="nav-item">
          <Button class="btn btn-outline-primary" @click="load(3)">
            저녁
          </Button>
        </li>
      </ul>
    </div>

    <div class="row">
      <div class="card col-5 dish" v-for="(row, index) in data" :key="index">
        <img class="card-img-top" :src="row.imgUrl" alt="Card image cap">
        <div class="card-body">
          <h5 class="card-title">{{ row.title }}</h5>
          <p class="card-text">{{ row.sub }}</p>
          <p class="card-text">{{ row.cal }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import moment from 'moment'

let map = {
  REST000003: "R4 레인보우",
  REST000004: "R4 밀키웨이",
  REST000005: "R4 오아시스",
  REST000006: "R5 5F",
  REST000007: "R5 B1F",
  REST000008: "R5 B2F",
  REST000009: "스마트",
  REST000010: "투게더",
  REST000011: "패밀리",
  REST000012: "폴라리스",
  REST000013: "R3 하모니",
  REST000014: "R3 쿠디",
  REST000016: "삼성미래기술캠퍼스",
  REST000048: "서울R&D 1단지",
  REST000049: "서울R&D 2단지",
  REST000062: "전자수원인재개발원"
};

export default {
  data () {
    return {
      data: {},
    }
  },
  watch:{
    $route () {
      let today = moment(new Date()).format('YYYYMMDD');
      axios
        .get('http://15.165.19.209:8080/menu?restCode=' + this.$route.params.code + '&date=' + this.$route.params.date
        + '&mealType=' + this.$route.params.mealType)
        .then(response => {
          console.log(response);
          this.data = response.data;
        });

    },
  },
  mounted () {
    axios
      .get('http://15.165.19.209:8080/menu?restCode=' + this.$route.params.code + '&date=' + this.$route.params.date
            + '&mealType=' + this.$route.params.mealType)
      .then(response => {
        console.log(response);
        this.data = response.data;
      });
  },
  methods : {
    load: function(mealType) {
      axios
        .get('http://15.165.19.209:8080/menu?restCode=' + this.$route.params.code + '&date=' + this.$route.params.date
                + '&mealType=' + mealType)
        .then(response => {
          this.data = response.data;
        });
    },
    getName: function(code) {
      return map[code];
    },
    today: function() {
      let today = moment().format('YYYYMMDD');
      this.$router.push("/menu/" + this.$route.params.code + "/" + today + "/" + this.$route.params.mealType);
    },
    tomorrow: function() {
      let tomorrow = moment().add(1, 'days').format('YYYYMMDD');
      this.$router.push("/menu/" + this.$route.params.code + "/" + tomorrow + "/" + this.$route.params.mealType);
    }
  }
}
</script>

<style scoped>
  .btn-circle {
    width: 30px;
    height: 30px;
    padding: 6px 0;
    border-radius: 15px;
    text-align: center;
    font-size: 12px;
    line-height: 1.42857;
  }

  .meal-type {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .nav-item {
    margin-right: 10px;
  }

  .dish {
    padding-top: 20px;
    margin: 10px;
  }

  .badge {
    margin-bottom: 5px;
  }
</style>



